import React from "react";
import Link from "gatsby-link";

const style = {
  breadcrumb: {
    color: "inherit",
    padding: "0 8px"
  }
};

export const createBreadcrumbLink = (link, index, len) => (
  <Link
    style={style.breadcrumb}
    to={link.path}
    onClick={e => {
      if (len - 1 === index) {
        e.preventDefault();
      }
    }}
  >
    {link.label}
  </Link>
);



